import { RootObject } from '@/model/rootObject'
import { GetUserListModel,UserAddModel,UserShowModel,AddUserModel,GetLogListModel } from '@/model/userManageModel'
import https from '@/utils/https'
import { RequestParams, ContentType, Method } from 'axios-mapper'
// 登录
export const loginRequest = (userInfo: RequestParams) => {
  return https(false).request<RootObject<string>>('/admin/login', Method.POST, userInfo, ContentType.json)
}
// 获取用户数据列表
export const getUserList = (page:any,size:any) => {
  return https().request<RootObject<GetUserListModel>>(`/admin/user/list?page=${page}&size=${size}`,Method.GET,null,ContentType.json)
}
//判断登录
export const checkLogin = () => {
  return https().request<RootObject<string>>('/admin/checkLogin',Method.GET,null,ContentType.json)
}

//根据用户名查询
export const getListByName = (userName: string)=>{
  return https().request<RootObject<GetUserListModel>>(`/admin/user/getName/${userName}`,Method.GET,null,ContentType.json)
}

//根据用户ID查询
export const getListById = (userId: string)=>{
  return https().request<RootObject<AddUserModel>>(`/admin/user/getById/${userId}`,Method.GET,null,ContentType.json)
}

//新增用户
export const addUser = (user:UserAddModel)=>{
  return https().request<RootObject<AddUserModel>>('/admin/user/add',Method.POST,user,ContentType.json);
}

//修改用户
export const updateUser = (user:UserShowModel)=>{
  return https().request<RootObject<UserShowModel>>('/admin/user/update',Method.POST,user,ContentType.json);
}

//删除用户
export const deleteUser = (userId: number)=>{
  return https().request<RootObject<string>>(`/admin/user/delete/${userId}`,Method.DELETE,null,ContentType.json);
}

//获取用户的文件记录
export const getLogById = (userId: number,page:number,size:number)=>{
  return https().request<RootObject<GetLogListModel>>(`/admin/history/getByUserId/${userId}?page=${page}&size=${size}`,Method.GET,null,ContentType.json);
}

