import { ActionTree, ActionContext } from 'vuex'
import { RootState } from '@/store'
import { UserState } from './state'
import { Mutations } from './mutations'
import { UserMutationTypes } from './mutation-types'
import { UserActionTypes } from './action-types'
import { loginRequest } from '@/apis/user'
import { removeToken, setToken } from '@/utils/cookies'
import router, { resetRouter } from '@/router'

type AugmentedActionContext = {
  commit<K extends keyof Mutations>(
    key: K,
    payload: Parameters<Mutations[K]>[1],
  ): ReturnType<Mutations[K]>
} & Omit<ActionContext<UserState, RootState>, 'commit'>

export interface Actions {
  [UserActionTypes.ACTION_LOGIN](
    { commit }: AugmentedActionContext,
    userInfo: { admin_name_or_email: string, admin_password: string }
  ): void
  [UserActionTypes.ACTION_RESET_TOKEN](
    { commit }: AugmentedActionContext
  ): void
  [UserActionTypes.ACTION_LOGIN_OUT](
    { commit }: AugmentedActionContext,
  ): void
}

export const actions: ActionTree<UserState, RootState> & Actions = {
  // 登录
  async [UserActionTypes.ACTION_LOGIN](
    { commit }: AugmentedActionContext,
    userInfo: { admin_name_or_email: string, admin_password: string }
  ) {
    let { admin_name_or_email, admin_password } = userInfo
    // 去除首尾空格
    admin_name_or_email = admin_name_or_email.trim()
    await loginRequest({ admin_name_or_email, admin_password }).then(async(res) => {
      if (res.code === "200") {
        console.log("登录成功:",res);        
        setToken(res.data)
        commit(UserMutationTypes.SET_TOKEN, res.data)
      }
    }).catch((err) => {
      console.log(err)
    })
  },
    
  [UserActionTypes.ACTION_RESET_TOKEN](
    { commit }: AugmentedActionContext) {
    removeToken()
    commit(UserMutationTypes.SET_TOKEN, '')
  },
  // 退出登录
  [UserActionTypes.ACTION_LOGIN_OUT](
    { commit }: AugmentedActionContext
  ) {
    console.log(commit)
    removeToken()
    commit(UserMutationTypes.SET_TOKEN, '')
    resetRouter()
  }
}
