import { RouteRecordRaw } from 'vue-router'
import Layout from '@/layout/Index.vue'

const MedicalIntroManageRouter: Array<RouteRecordRaw> = [
  {
    path: '/medicalIntroManage',
    component: Layout,
    redirect: '/medicalIntroManage/tablemanage',
    name: 'medicalIntroManage',
    meta: {
      title: 'medicalIntroManage',
      icon: '#icontable'
    },
    children: [
      {
        path: 'tablemanage',
        component: () => import(/* webpackChunkName: "dashboard" */ '@/views/introManage/introManage.vue'),
        meta: {
          title: 'medicalIntroManage',
          icon: '#icondashboard',
          affix: true
        }
      },
      {
        path: 'tinymce',
        component: () => import(/* webpackChunkName: "dashboard" */ '@/views/introManage/EditContent.vue'),
        meta: {
          title: 'medicalEdit',
          icon: '#icondashboard',
          hidden: true
        }
      }
    ]
  }
]

export default MedicalIntroManageRouter
