import { RouteRecordRaw } from 'vue-router'
import Layout from '@/layout/Index.vue'

const FileManageRouter: Array<RouteRecordRaw> = [
  {
    path: '/filemanage',
    component: Layout,
    redirect: '/filemanage/tablemanage',
    name: 'filemanage',
    meta: {
      title: 'filemanage',
      icon: '#icontable'
    },
    children: [
      {
        path: 'tablemanage',
        component: () => import(/* webpackChunkName: "dashboard" */ '@/views/filemanage/filemanage.vue'),
        meta: {
          title: 'filemanage',
          icon: '#icondashboard',
          affix: true
        }
      }
    ]
  }
]

export default FileManageRouter
