import { RouteRecordRaw } from 'vue-router'
import Layout from '@/layout/Index.vue'

const userManageRouter: Array<RouteRecordRaw> = [
  {
    path: '/usermanage',
    component: Layout,
    redirect: '/usermanage/tablemanage',
    name: 'usermanage',
    meta: {
      title: 'usermanage',
      icon: '#icontable'
    },
    children: [
      {
        path: 'tablemanage',
        component: () => import(/* webpackChunkName: "dashboard" */ '@/views/usermanage/usermanage.vue'),
        name: 'TableManage',
        meta: {
          title: 'usermanage',
          icon: '#icondashboard',
          affix: true
        }
      },
      {
        path: 'showlog/:user_id',
        component: () => import(/* webpackChunkName: "dashboard" */ '@/views/usermanage/ShowLogTable.vue'),
        name: 'ShowLog',
        meta: {
          title: 'showlog',
          hidden: true
        }
      }
    ]
  }
]

export default userManageRouter
