import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
// import { useI18n } from 'vue-i18n'
import router from '@/router'
import { RouteLocationNormalized } from 'vue-router'
import { useStore } from './store'
import whiteList from './config/default/whitelist'
import { checkLogin } from '@/apis/user'
import { ElMessage } from 'element-plus'
import { UserActionTypes } from './store/modules/user/action-types'
// import settings from '@/config/default/setting.config'
NProgress.configure({ showSpinner: false })

router.beforeEach(async (to: RouteLocationNormalized, _: RouteLocationNormalized, next: any) => {
      NProgress.start()
      const store = useStore()
      const token = store.state.user.token
      if (token) {//如果需要跳转 ，往下走（1）
            //判断是否登录
            try {
                  const result = await checkLogin()
                  if (result.code === '200') {
                        next()
                  } else {
                        store.dispatch(UserActionTypes.ACTION_RESET_TOKEN, undefined)
                        ElMessage.error(result.msg || 'Has Error')
                        next(`/login?redirect=${to.path}`)
                        NProgress.done()
                  }
            } catch (error) {
                  store.dispatch(UserActionTypes.ACTION_RESET_TOKEN, undefined)
                  ElMessage.error(error || 'Has Error')
                  next(`/login?redirect=${to.path}`)
                  NProgress.done()
            }
      } else {
            // Has no token
            if (whiteList.indexOf(to.path) !== -1) {
                  // In the free login whitelist, go directly
                  next()
            } else {
                  // Other pages that do not have permission to access are redirected to the login page.
                  next(`/login?redirect=${to.path}`)
                  NProgress.done()
            }
      }
})

router.afterEach((to: RouteLocationNormalized) => {
      console.log(to)
      // Finish progress bar
      // hack: https://github.com/PanJiaChen/vue-element-admin/pull/2939
      NProgress.done()

      // set page title
      // document.title = getPageTitle(to.meta.title)
})
